body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 98vh;
  box-sizing: border-box;
  /* this is stop window from bouncing */
  overscroll-behavior: none;
  /* this is stop window from bouncing */
  overflow-y: scroll;
}

.title-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  flex: 0 80px auto;
  overflow: hidden;
  background-color: black;
  color: white;
}

.zoompan-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  flex: 1 1 auto;
  overflow: hidden;
}

.el-img {
  width: 100%;
  height: 100%;
}
.el-norm-container-login {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-color: chocolate;
  height: 100%;
}
.el-norm-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: orange;
  border: solid 1px gray;
  width: 100%;
  height: 100%;
  border-radius: 2px;
}

.el-norm-container2 {
  background-color: crimson;
}
.el-norm-container-text {
  font-family: "Arial";
  background-color: crimson;
  background-color: white;
  align-items: flex-start;
  text-align: justify;
  border: none;
}
